/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00; }
  a:visited {
    color: #00cc00; }
  a:hover, a:focus, a:active {
    color: midnightblue; }
  a:focus {
